// IMPORTATION DES DONNES DES LOGEMENTS DEPUIS LE FICHIER LOGEMENTS.JSON 
import data from '../datas/logements.json'; 

// IMPORTATION DES DONNES DE LA PAGE APROPOS DEPUIS LE FICHIER APROPOS.JSON 
import dataAccordions from '../datas/apropos.json'; 

export default class Api {
  // RCUPRER LES DONNES DES LOGEMENTS 
  getLogements() {
    return data; // Retourne les donnes des logements
  }

  // RCUPRER LES DONNES D'UN LOGEMENT SPCIFIQUE EN UTILISANT L'ID PASS EN PARAMTRE
  getLogement(id) {
    return data.find(current => current.id === id); // Retourne les donnes du logement correspondant  l'ID spcifi
  }

  // RCUPRER LES DONNES DE LA PAGE APROPOS
  getDataAccordions() {
    return dataAccordions; // Retourne les donnes de la page apropos
  }
}
